/* Error message size */
p {
  margin: 8px;
  font-size: 0.6rem;
}

.form-control invalid{
  margin-bottom: 1rem;
  display: flex;
  margin: auto;
  padding: 2rem;
  column-gap: 1rem;
  flex-wrap: wrap;
  justify-content: center;
}

.form-control input,
.form-control label {
  /* display: block; */
  display: flex;
  /* justify-content: center; */
}

/* Label font size */
.form-control label {
  font-size: 0.9rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

/* Input BG Colour */
input {
  background-color: #C6FCEC;
}

input,
input::placeholder {
  font-size: 0.9rem;
}

.form-control input,
.form-control select {
  display: flex;
  justify-content: center;
  font: inherit;
  padding: 0.5rem;
  border-radius: 0.5rem;
  border: 1px solid #ccc;
  width: 15rem;
  max-width: 100%;
}

.form-control input:focus {
  outline: none;
  border-color: #240370;
  background-color: #C6FCEC;
}

.control-group {
  display: flex;
  margin: auto;
  padding: 0.5rem;
  /* column-gap: 1rem; */
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

/* To provide additional padding & grouping for uniform text alignment*/
.label-group {
  margin-right: 1rem;
  padding: 0.5rem;
}

/* To help left-align all the labels, by creating separate fixed boxes for each */
.fixed-box-size {
  width: 10.6rem;  /* To the largest width */
  text-align: left;
}


/* Selecting our form-control class and making issues with flex display centering input field.. */
/* .control-group .form-control {
  min-width: 15rem;
  flex: 1;
} */


button {
  display: inline-flex;
  align-items: center;
  font: inherit;
  padding: 0.75rem 1.5rem;
  background-color: #C6FCEC;
  color: #98B0A9;
  border: none;
  border-radius: 0.5rem;
  box-shadow: 0 2px 16px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

button:hover,
button:active {
  background-color: #8df3d4;
}

button:disabled,
button:disabled:hover,
button:disabled:active {
  background-color: #ccc;
  color: #292929;
  border-color: #ccc;
  cursor: not-allowed;
}

.form-actions {
  text-align: center;
}

.form-actions button {
  margin-left: 1rem;
  margin-top: 1rem;
  font-size: 0.9rem;
}

.invalid input {
  border: 1px solid #8b1010;
  background-color: #f0f0f0;
}

.form-control .invalid input{
  border: 1px solid #b40e0e;
  background-color: #fddddd;
}

.invalid input:focus {
  border-color: #ff8800;
  background-color: #fbe8d2;
}

.error-text {
  color: #b40e0e;
}


