.backdrop {
    /** backdrop color and have z-index be one to override or be on top of current page*/
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 1;
    position: fixed;
    width: 100%;
    height: 100vh;
    /* Set position of where the backdrop starts? */
    top: 0;
    left: 0;
}

.modal {
    /* same z-index as backdrop (show in fg) */
    /* default padding is 16 all 4 sides */
    /* As is with border being existent in dialog html elements */
    border: none;
    border-radius: 0.4rem;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    padding: 0;
    overflow: hidden;
    z-index: 1;
}