.container {
  display: flex;
  max-width: 100%;
  justify-content: center;
}

.header {
  max-width: 100%;
  margin-top: 1rem;
  /* margin: auto; */
  padding: 1rem;
  display: flex;
  border: solid 0.2rem;
  border-radius: 0.5rem;
  border-color: #c6fcec;
  /* background-color: #C6FCEC; */
  text-decoration: none;
}

a {
  text-decoration: none;
}

/* For helping us space out our <ul> block element which always starts on new line. So it'd stack on top "Home" and 
                                                                                                           "About" 
  Flex fixes this issue and having a gap to space them out. 
  */
.list {
  display: flex;
  gap: 1rem;
}

/* This helps us remove default text decorators like underlines
  Then we can apply a light color variance */
.list a {
  text-decoration: none;
  color: #98B0A9;
  padding: 0.4rem;
}

.list a:hover,
.list a.active {
  /* During hover and once it is active, increase the lumen more by color power and underline the hovered text on header. */
  /* background-color: var(--color-primary-900); */
  background-color: #8df3d4;
  border-radius: 0.4rem;
  text-decoration: none;
}