/* At sizes greater than 700px, default style */
@media screen and (min-width: 700px) {
    .expense-history-container {
        /** Imagine what kind of display we want, grid parent container and non-flex cards stick with one */
        display: grid;
        grid-template-columns: repeat(4, 20%);
        justify-content: center;
        max-width: 60rem;
        margin: 1rem auto;
        /** Top/Bot 16px spacing */
        list-style: none;
        /** erdicate decimal listings for each ul item*/
        gap: 1rem;
        /** space between each column */
    }
    .expense-history-empty-container{
        display: flex;
        flex-direction: column;
        max-width: 60rem;
        margin: 1rem auto;
        align-items: center;
        color: white;
        justify-content: center;
    }
}

/* At sizes of screen being less than 700px, follow the new container style */
@media screen and (max-width: 700px) {
    .expense-history-container {
        display: flex;
        flex-direction: column;
        width: 20% auto 20% auto;
        list-style: none;
        gap: 0.5rem;
        justify-content: center;
        align-items: center; /** Center items along the column line*/
    }
    .expense-history-empty-container{
        display: flex;
        flex-direction: column;
        max-width: 60rem;
        margin: 1rem auto;
        align-items: center;
        color: white;
        justify-content: center;
    }
}